.ant-menu-overflow {
    margin-top: -29px;
}

.layout-container {
    max-width: 1200px; /* 전체 너비를 900px로 설정 */
    margin: 0 auto; /* 중앙 정렬 */
    padding: 0 40px; /* 좌우 패딩 */
    min-height: 100vh;
}

.ant-menu-horizontal > .ant-menu-item,
.ant-menu-horizontal > .ant-menu-submenu {
    margin-right: 20px; /* 메뉴 항목 간격을 20px로 조정 */
}

.ant-menu-horizontal {
    font-size: 24px; /* 메뉴 폰트 크기를 24px로 설정 */
}